@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@400;700&display=swap);
.slider__item{min-height:100vh;background-repeat:no-repeat;background-size:cover;background-position:top center;display:flex;align-items:center;justify-content:space-around;z-index:2;padding-top:100px}.slider__item::before{position:absolute;content:"";width:100%;height:100%;top:0;bottom:0;background:rgba(0,0,0,.5);z-index:1}.slider__item-content{max-width:600px;color:#fff;z-index:10}.slider__item-title{font-size:40px;font-weight:700;margin-bottom:20px}.slider__item-desc{font-size:18px;line-height:1.3em}.slider__item-poster{z-index:10;border-radius:20px}.slider__item-button{padding:10px 40px;border:2px solid #fff;margin-top:20px;border-radius:20px;position:relative}.slider__item-button:hover{background:rgba(0,0,0,.5)}.slider__item-button:active{bottom:-1px}
.header{position:fixed;z-index:99;color:#fff;padding:15px;max-width:1920px;width:100%;left:0;right:0;margin:0 auto;height:100px;display:flex;align-items:center;border-bottom:1px solid #9b9b9b}.header-inner{width:100%;display:flex;align-items:center;justify-content:space-between}.header__logo{display:flex;align-items:center;cursor:pointer}.header__logo-title{font-size:30px;font-weight:700;margin-left:10px}.nav__list{display:flex}.nav__list-item{margin-right:15px;font-size:20px;font-weight:700;cursor:pointer;transition:.5s}.nav__list-item:last-child{margin-right:0}.nav__list-item:hover{color:#e44f4f}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}*{box-sizing:border-box}html,body{height:100%;font-family:"Balsamiq Sans",cursive;background:#444b54}ol,ul{list-style:none}button{border:none;outline:none;background:none;cursor:pointer;font-family:"Balsamiq Sans",cursive;color:#fff;font-size:16px;transition:.5s}
